import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    content: {
      wolf: {
        audio: '',
        backgroundImg: '',
        mapDetail: [{
          name: '',
          image: '',
          title: '',
          description: ''
        },
        {
          name: '',
          image: '',
          title: '',
          description: ''
        }
        ]
      },
      landscape: {
        audio: '',
        backgroundImg: '',
        maps: [{
          withoutWolves: {
            mapImage: ''
          }
        },
        {
          withWolves: {
            mapImage: ''
          }
        }]
      }
    },
    helpers: {
      overflow: 'visible'
    },
    wolfmap: {
      currentDetail: null,
      currentView: 'appWolfmap',
      animation: 'fade'
    }
  },

  getters: {
    helperState: state => state.helpers,
    wolfContent: state => state.content.wolf,
    wolfState: state => state.wolfmap
  },

  mutations: {
    setProp (state, payload) {
      let parent = payload.parent
      let key = payload.key
      let value = payload.value

      state[parent][key] = value
    },

    updateProps (state, payload) {
      let parent = payload.parent
      let updates = payload.updates
      state[parent] = updates
    }
  },

  actions: {
    getLocalData ({ commit }) {
      Vue.http.get('./static/data/content.json')
      .then(response => {
        console.log(response)
        return response
      })
      .then(data => {
        let payload = {
          parent: 'content',
          updates: data.body
        }
        commit('updateProps', payload)
      })
      .catch(err => {
        console.log(err)
      })
    },

    setProp ({ commit }, payload) {
      commit('setProp', payload)
    },

    updateProps ({ commit }, payload) {
      commit('updateProps', payload)
    }
  }
})



