<template>
  <div :style="{ overflow: helperState.overflow }" style="height: 100vh">
    <app-audio :src="'./static/audio/' + content.wolf.audio"></app-audio>
    <app-background :backgroundImg="'./static/images/' + content.wolf.backgroundImg"></app-background>

    <transition 
    :name="wolfState.animation" 
    mode="out-in" 
    v-on:after-enter="setProp('helpers', 'overflow', 'visible')"
    v-on:before-leave="setProp('helpers', 'overflow', 'hidden')" 
    v-on:after-leave="setProp('helpers', 'overflow', 'hidden')">
      <component :is="wolfState.currentView"></component>
    </transition>

  </div>
</template>



<script>
import appAudio from './../partials/audio'
import appBackground from './../partials/background'
import appWolfmap from './../explore/wolf-map'
import appWolfDetail from './../explore/wolf-detail'
import content from './../../../static/data/content.json'
export default {
  components: {
    appAudio,
    appBackground,
    appWolfmap,
    appWolfDetail
  },

  data () {
    return {
      content,
      overflow: 'visible'
    }
  },

  computed: {
    helperState () {
      return this.$store.getters.helperState
    },

    wolfState () {
      return this.$store.getters.wolfState
    }
  },

  methods: {
    setProp (parent, key, value) {
      let payload = {
        parent: parent,
        key: key,
        value: value
      }
      this.$store.dispatch('setProp', payload)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "./../../assets/main.scss";

</style>



