<template>
  <div>
    <app-audio :src="'./static/audio/' + content.landscape.audio"></app-audio>
    <app-background :backgroundImg="'./static/images/' + content.landscape.backgroundImg"></app-background>

    <div class="container" style="padding-top: 8em;">

      <div class="row">
        <div class="col text-center">
          <transition name="fade" mode="out-in">
            <h2 :key="generateKey()">{{ landscapeTitle }}</h2>
          </transition>
          <toggle-button v-model="landscapeState" :sync="true" />
        </div>
      </div>

      <transition name="fade" mode="out-in">
        <app-landcape-map 
        :content="content.landscape.maps[1].withWolves" 
        v-if="landscapeState" 
        :key="generateKey()" />
        <app-landcape-map 
        :content="content.landscape.maps[0].withoutWolves" 
        v-else 
        :key="generateKey()" />
      </transition>

    </div>

  </div>
</template>

<script>
import appAudio from './../partials/audio'
import appBackground from './../partials/background'
import appLandcapeMap from './../landscape/landscape-map'
import content from './../../../static/data/content.json'
import MixinHelper from './../mixins/helper'
export default {
  components: {
    appAudio,
    appBackground,
    appLandcapeMap
  },

  mixins: [MixinHelper],

  data () {
    return {
      content,
      landscapeState: false
    }
  },

  computed: {
    landscapeTitle () {
      return this.landscapeState ? 'A World without Wolves' : 'A World with Wolves'
    }
  }
}
</script>

<style lang="scss" scoped>
@import "./../../assets/main.scss";
</style>



